import React from 'react';
import styled from "styled-components";
import bgImg from "../../../assets/image/imageBgc/bgc.webp";
import {MyContainer} from "../../../components/MyContainer";
import {FlexContainer} from "../../../components/FlexContainer";
import {font} from "../../../styles/Common";
import {Icon} from "../../../components/Icon";

export const MainSection: React.FC = () => {
    return (
        <StyledMainSection>
            <MyContainer>
                <FlexContainer justify={"space-between"} align={"center"} direction={"column"}>
                   <div>
                       <MainTitle>
                           Салон красоты
                       </MainTitle>
                       <Name>«Delote-Beauty» <br/> на Крестовском</Name>
                    </div>
                    <ScrollWrap>
                        <Icon iconId={"mouse"}
                              width={"20px"}
                              height={"30px"}
                              viewBox={"0 0 20 30"}/>
                        <span>Прокрутите вниз</span>
                    </ScrollWrap>
                </FlexContainer>

            </MyContainer>
        </StyledMainSection>
    );
};

const MainTitle = styled.h1`

`
const Name = styled.p`

`
const StyledMainSection = styled.section`
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.50) 100%), url(${bgImg}), lightgray 50%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100dvh;
  display: flex;
  padding: 360px 0 100px;

  ${MainTitle}, ${Name} {
    ${font({family: "'EB Garamond'", weight: 700, fontMax: 72, fontMin: 36})};
  }
}
`
const ScrollWrap = styled.div`
  display: flex;
  align-items: center;
  column-gap: 20px;
  
  span{
    font-family: "Cormorant Garamond" , sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.4; 
  }
`
