import React from 'react';
import styled from "styled-components";
import {Item} from "../item/Item";
import {servicePriceType} from "../Price";

type ListPricePropsType = {
    servicePriceData: Array<servicePriceType>
}
export const ListPrice: React.FC<ListPricePropsType> = (props: ListPricePropsType) => {
    return (
        <StyledListPrice>
            {props.servicePriceData.map((s, index) => {
                return (
                    <ItemPrice key={index}>
                        <Item title={s.title} text={s.text} price={s.price} />
                    </ItemPrice>
                )
            })}

        </StyledListPrice>
    );
};

const StyledListPrice = styled.ul`
  display: flex;
  flex-direction: column;
  max-width: 526px;
  width: 100%;
  gap: 40px;
`

const ItemPrice = styled.li`
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
`