import React from 'react';
import styled from "styled-components";
import {TitleListFooter} from "../titleListFooter/TitleListFooter";
import {Icon} from "../../../components/Icon";
import {SocialNetWorkType} from "../Footer";

type SocialNetWorkPropsType = {
    listItems: Array<SocialNetWorkType>
    title: string
}
export const SocialNetWork: React.FC<SocialNetWorkPropsType> = (props: SocialNetWorkPropsType) => {
    return (
        <StyledSocialNetWork>
            <TitleListFooter>{props.title}</TitleListFooter>
            <ul>

                {props.listItems.map((i, index) => {
                    return (
                        <ItemList key={index}>
                            <a href={i.href} target={"_blank"}>

                                <Icon
                                    iconId={i.iconId}
                                    width={i.width}
                                    height={i.height}
                                    viewBox={i.viewBox}/>
                            </a>
                        </ItemList>
                    )
                })}

            </ul>
        </StyledSocialNetWork>
    );
};


const StyledSocialNetWork = styled.div`
    
`
const ItemList = styled.li`

`