import React from 'react';
import styled from "styled-components";
import {MyContainer} from "../../../components/MyContainer";
import {FlexContainer} from "../../../components/FlexContainer";
import {Icon} from "../../../components/Icon";

export const Description: React.FC = () => {
    return (
        <StyledDescription>
            <MyContainer>
                <FlexContainer direction={"column"} align={"center"}>
                    <Icon iconId={"commas"}
                    width={"80"}
                    height={"64"}
                    viewBox={"0 0 80 64"}/>
                    <Text>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. In arcu nibh vitae amet. Ipsum,
                        pharetra donec ornare velit. Id at quisque accumsan risus ac ipsum ut. Sit elit, facilisi proin
                        non malesuada sociis tristique. Viverra augue lorem ut quisque quam tortor, malesuada iaculis.<br/>
                        Et elementum at nulla venenatis, faucibus integer. Auctor neque eros, viverra rutrum. Fames
                        ultrices condimentum tortor nec penatibus. Velit imperdiet sapien fringilla vestibulum sit
                        fames.
                    </Text>
                </FlexContainer>
            </MyContainer>
        </StyledDescription>
    );
};

const StyledDescription = styled.section`
margin: 120px 0 115px;
`
const Text = styled.p`
  padding-top: 60px;
  max-width: 856px;
  width: 100%;
  text-align: center;
  font-size: 27px;
  font-weight: 700;
`