import React from 'react';
import styled from "styled-components";
import { TitleListFooter} from "../titleListFooter/TitleListFooter";
import {ContactType} from "../Footer";


type ContactPropsType = {
    listItems: Array<ContactType>
    title: string
}

export const Contact: React.FC<ContactPropsType> = (props: ContactPropsType) => {
    return (
        <StyledContact>
            <TitleListFooter>{props.title}</TitleListFooter>
            <ul>

                {props.listItems.map((i, index) => {
                        return (
                            <ItemList key={index}>
                                <a href={i.href} >{i.text}</a>
                            </ItemList>
                        )
                    })}
            </ul>
        </StyledContact>
    );
};

const StyledContact = styled.div`
ul {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
}
`

const ItemList = styled.li`

`