import React from 'react';
import styled from "styled-components";
import {TitleListFooter} from "../titleListFooter/TitleListFooter";


type TimeWorkPropsType = {
    listItems: Array<string>
    title: string
}
export const TimeWork: React.FC<TimeWorkPropsType> = (props: TimeWorkPropsType) => {
    return (
        <StyledTimeWork>
            <TitleListFooter>{props.title}</TitleListFooter>
            <ul>
                {props.listItems.map((i, index) => {
                    return (
                        <ItemList key={index}>{i}</ItemList>
                    )
                })}
            </ul>
    </StyledTimeWork>
    );
};

const StyledTimeWork = styled.div`
  ul {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
  }
`
const ItemList = styled.li`
  font-size: 18px;
  font-weight: 400;
  line-height: 1.3;
`