import React, {ReactNode} from 'react';
import {Header} from "./header/Header";
import {Footer} from "./footer/Footer";


type LayoutPropsType = {
    children: ReactNode
}
export const Layout: React.FC<LayoutPropsType> = (props) => {
    return (
        <>
            <Header/>
            <>
                {props.children}
            </>
            <Footer/>
        </>
    );
};

