import React from 'react';
import {Icon} from "../../../components/Icon";
import {Menu} from "../menu/Menu";
import {ItemType} from "../Header";
import styled from "styled-components";
import {FlexContainer} from "../../../components/FlexContainer";


type HeaderMenuPropsTYpe = {
    firstNavList: Array<ItemType>
    secondNavList: Array<ItemType>
}
export const DesktopMenu: React.FC<HeaderMenuPropsTYpe> = (props: HeaderMenuPropsTYpe) => {
    return (
        <StyledDesktopMenu>
            <FlexContainer justify={"space-between"}>
                <a href={"https://www.instagram.com/"} target={"_blank"}>
                    <Icon iconId={"instagram"}
                          width={"26px"}
                          height={"26px"}
                          viewBox={"0 0 26 26"}
                    />
                </a>
                <Menu menuItems={props.firstNavList}/>
                <IconWrap>
                    <Icon iconId={"omega"}
                          width={"40px"}
                          height={"40px"}
                          viewBox={"0 0 40 40"}/>
                </IconWrap>
                <Menu menuItems={props.secondNavList}/>
                <IconWrap>
                    <Icon iconId={"btn"}
                          width={"50px"}
                          height={"21px"}
                          viewBox={"0 0 50 21"}/>
                </IconWrap>
            </FlexContainer>
        </StyledDesktopMenu>
    );
};


const StyledDesktopMenu = styled.nav`
  ${FlexContainer} {
    column-gap: 10px;
  }

`
const IconWrap = styled.div`
`