import React from 'react';
import styled from "styled-components";


type ItemPropsType = {
    title: string
    text: string
    price: string
}
export const Item: React.FC<ItemPropsType> = (props: ItemPropsType) => {
    return (
        <StyledItem>
           <div>
      <TitleService>{props.title}</TitleService>
      <PriceService>{props.price}</PriceService>
           </div>
      <Bonus>{props.text}</Bonus>
        </StyledItem>
    );
};

const StyledItem = styled.div`
div{
  width: 100%;
  display: flex;
  justify-content: space-between;
}
`
const Bonus = styled.p`
  padding: 10px 0 20px;
`
const TitleService = styled.h3`

`
const PriceService = styled.span`
  font-size: 27px;
  font-weight: 700;
  line-height: 1.3;
`
