import React from 'react';
import styled from "styled-components";
import {Photo} from "../../../../components/Photo";
import {WrapperPhoto} from "../../../../components/WrapperPhoto";
import {theme} from "../../../../styles/Theme";

type WorkerPropsType = {
    name: string
    job: string
    src: string
}

export const Worker: React.FC<WorkerPropsType> = (props: WorkerPropsType) => {
    return (
        <StyledWorker>
            <WrapperPhoto  width={"400px"}
                           height={"478px"}>
                <Photo src={props.src}
                       alt={props.job}
                       width={"400px"}
                       height={"478px"}/>
            </WrapperPhoto>

            <Name>{props.name}</Name>
            <TitleJob>{props.job}</TitleJob>
        </StyledWorker>
    );
};

const StyledWorker = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  
  @media ${theme.media.mobile}{
    ${Photo}{
      width: 300px;
      height: 378px;
    }
    ${WrapperPhoto}{
      &::before{
        width: 300px;
        height: 378px;
        top: -12px;
        left: 12px;
      }
    }
  }
`
const Name = styled.h3`
margin: 30px 0 10px;
`
const TitleJob = styled.span`
  font-size: 18px;
  font-weight: 400;
  line-height: 1.3;
`