import React from 'react';
import styled from "styled-components";
import {MyContainer} from "../../components/MyContainer";
import {FlexContainer} from "../../components/FlexContainer";
import {theme} from "../../styles/Theme";
import {Icon} from "../../components/Icon";
import {Contact} from "./contact/Contact";
import {TimeWork} from "./timeWork/TimeWork";
import {SocialNetWork} from "./socialNetWork/SocialNetWork";

export type ContactType ={
    text: string
    href: string
}

export const contactData: Array<ContactType>= [
    {text:"+7 (812) 123-45-67", href: "tel: +7 (812) 123-45-67"},
    {text:"+7 (911) 123-45-67", href: "tel: +7 (911) 123-45-67"},
    {text:"Новоостровский проспект,  дом 36 лит. С", href: "#"}
];

export const timeWorksData = [
    "C 10:00 до 21:00 (Пн-Пт)",
    "С 11:00 до 20:00 (Сб-Вс)"
]

export type SocialNetWorkType = {
    iconId: string
    href: string
    width?: string
    height?: string
    viewBox?: string
}

const socialNetWorkData: Array<SocialNetWorkType> = [
    {iconId:"instagram", width: "38px", height: "38px", viewBox: "0 0 38 38", href: "https://www.instagram.com/"}
]


export const Footer: React.FC = () => {
    return (
        <StyledFooter>
            <MyContainer>
                <FlexContainer justify={"space-between"} wrap={"wrap"}>
                    <Icon iconId={"omega"}/>
                    <Contact listItems={contactData} title={"Контакты"}/>
                    <TimeWork listItems={timeWorksData} title={"Режим работы"}/>
                    <SocialNetWork listItems={socialNetWorkData} title={"Мы в соцсетях"}/>

                </FlexContainer>
                <Copyright>© 2023 Evgeniy Huletskiy</Copyright>
            </MyContainer>
        </StyledFooter>
    );
};

const StyledFooter = styled.footer`
  margin-top: 100px;
  min-height: 368px;
  justify-content: space-between;
  ${FlexContainer} {
    padding: 80px 0;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  }

  @media ${theme.media.tablet} {
    ${FlexContainer} {
      justify-content: space-around;
      gap: 20px;
    }
  }

  @media ${theme.media.mobile} {
    ${FlexContainer} {
      flex-direction: column;
      align-items: center;
      gap: 40px;
    }
  }
`


const Copyright = styled.p`
  padding: 30px 0;
  color: ${theme.colors.font};
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.4;
  opacity: 0.5;
`