import React from 'react';
import styled from "styled-components";
import {MyContainer} from "../../components/MyContainer";
import {PATH} from "../../pages/Pages"
import {DesktopMenu} from "./desktopMenu/DesktopMenu";
import {MobileMenu} from "./mobileMenu/MobileMenu";


export type ItemType = {
    title?: string
    id?: string
    path: string
}

// export type StatusType = "main" | "staff" | "cosmetic" | "sale" | "comment" | "contact";

const firstNavList: Array<ItemType> = [
    {title: "Главная", id: "main", path: PATH.MAIN},
    {title: "Мастера", id: "staff", path: PATH.OUR_TEAM},
    {title: "Косметика", id: "cosmetic", path: ""},
];


const secondNavList: Array<ItemType> = [
    {title: "Акции", id: "sale", path: PATH.PRICE},
    {title: "Отзывы", id: "comment", path: ""},
    {title: "Контакты", id: "contact", path: PATH.CONTACTS},
];
export const Header: React.FC = () => {

    const [width, setWidth] = React.useState(window.innerWidth);
    const breakpoint = 768;

    React.useEffect(() => {
        const handleWindowResize = () => setWidth(window.innerWidth);
        window.addEventListener("resize", handleWindowResize);
        return () => window.removeEventListener("resize", handleWindowResize)
    }, [])
    
    return (
        <StyledHeader>
            <MyContainer>
                {width < breakpoint ? <MobileMenu firstNavList={firstNavList} secondNavList={secondNavList}/>
                                    : <DesktopMenu firstNavList={firstNavList} secondNavList={secondNavList}/>}

            </MyContainer>
        </StyledHeader>
    );
};


const StyledHeader = styled.header`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  padding: 30px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);

`