import React from 'react';
import styled from "styled-components";
import {MyContainer} from "../../../components/MyContainer";
import {SectionTitle} from "../../../components/SectionTitle";
import {FlexContainer} from "../../../components/FlexContainer";
import worker1 from "../../../assets/image/imageTeam/unsplash_RBerxXPnZPE.webp";
import worker2 from "../../../assets/image/imageTeam/unsplash_IF9TK5Uy-KI.webp";
import worker3 from "../../../assets/image/imageTeam/unsplash_52fj00DaOPA.webp";
import worker4 from "../../../assets/image/imageTeam/unsplash_sLGYaQ_stMM.webp";
import worker5 from "../../../assets/image/imageTeam/unsplash_TSZo17r3m0s.webp";
import {Worker} from "./worker/Worker";

export type WorkerType = {
    photo: string
    name: string
    job: string
}


const workersData: Array<WorkerType> = [
    {photo: worker1, name: "Елена Васильевна", job: "Визажист"},
    {photo: worker2, name: "Татьяна Петровна", job: "Маникюрщица"},
    {photo: worker3, name: "Наталья Ивановна", job: "Визажист"},
    {photo: worker4, name: "Анастасия Романовна ", job: "Парикмахер"},
    {photo: worker5, name: "Светлана Александровна", job: "Визажист"},
    {photo: worker1, name: "Елена Васильевна", job: "Косметолог"},
]

export const Workers: React.FC = () => {
    return (
        <StyledWorkers>
            <MyContainer>
                <SectionTitle>Наши мастера</SectionTitle>
                <FlexContainer wrap={"wrap"} justify={"space-around"}>
                    {workersData.map((w, index) => {
                        return (
                            <Worker key={index}
                                    src={w.photo}
                                    name={w.name}
                                    job={w.job}
                            />
                        )
                    })}
                </FlexContainer>
            </MyContainer>

        </StyledWorkers>
    );
};

const StyledWorkers = styled.section`
  padding-top: 100px;
${FlexContainer} {
  gap: 60px 40px;
}
`