import React from 'react';
import styled from "styled-components";
import {Photo} from "../../../../components/Photo";
import {WrapperPhoto} from "../../../../components/WrapperPhoto";
import {theme} from "../../../../styles/Theme";
import {font} from "../../../../styles/Common";

type ServicePropsType = {
    photo: string
    title: string
}

export const Service: React.FC<ServicePropsType> = (props: ServicePropsType) => {
    return (
        <StyledService>
            <WrapperPhoto width={"396px"} >
                <Photo width={"396px"} src={props.photo} alt={props.title}/>
            </WrapperPhoto>
            <TitleService>{props.title}</TitleService>
        </StyledService>
    );
};


const StyledService = styled.div`
@media ${theme.media.mobile}{
  ${Photo}{
    width: 300px;
    height: 214px;
  }
  ${WrapperPhoto}{
    &::before{
      width: 300px;
      height: 214px;
      top: -12px;
      left: 12px;
    }
  }
}
`


const TitleService = styled.h3`
  margin-top: 30px;
  ${font({family: '"EB Garamond", sans-serif', weight: 400, lineHeight: 1.2, fontMax: 30, fontMin: 24})}
  //font-family: "EB Garamond", sans-serif;
  //font-size: 30px;
  //font-weight: 400;
  //line-height: 1.2;
`
