import React, {useState} from 'react';
import styled from "styled-components";
import {MyContainer} from "../../../components/MyContainer";
import {FlexContainer} from "../../../components/FlexContainer";
import {SectionTitle} from "../../../components/SectionTitle";
import photoWork1 from "../../../assets/image/imageWorks/work-1.webp";
import photoWork2 from "../../../assets/image/imageWorks/work-2.webp";
import photoWork3 from "../../../assets/image/imageWorks/work-3.webp";
import photoWork4 from "../../../assets/image/imageWorks/work-4.webp";
import photoWork5 from "../../../assets/image/imageWorks/work-5.webp";
import photoWork6 from "../../../assets/image/imageWorks/work-6.webp";
import photoWork7 from "../../../assets/image/imageWorks/work-7.webp";
import photoWork8 from "../../../assets/image/imageWorks/work-8.webp";
import photoWork9 from "../../../assets/image/imageWorks/work-9.webp";
import {Photo} from "../../../components/Photo";
import {AnimatePresence, motion} from "framer-motion";


import {TabMenu} from "./tabMenu/TabMenu";
import {theme} from "../../../styles/Theme";

export type TabStatusType = "all" | "hairdressing" | "manicure" | "makeUp";

export type TabMenuItemType = {
    title: string
    status: TabStatusType
}


const dataWorks = [
    {src: photoWork1, type: "hairdressing", id: 1},
    {src: photoWork2, type: "makeUp", id: 2},
    {src: photoWork3, type: "manicure", id: 3},
    {src: photoWork4, type: "hairdressing", id: 4},
    {src: photoWork5, type: "hairdressing", id: 5},
    {src: photoWork6, type: "makeUp", id: 6},
    {src: photoWork7, type: "manicure", id: 7},
    {src: photoWork8, type: "makeUp", id: 8},
    {src: photoWork9, type: "makeUp", id: 9},
]
const tabMenuItems: Array<TabMenuItemType> = [
    {title: "Показать всё", status: "all"},
    {title: "Парикмахерские услуги", status: "hairdressing"},
    {title: "Маникюр", status: "manicure"},
    {title: "Визаж", status: "makeUp"},
]
export const Works: React.FC = () => {

    const [currentFilterStatus, setCurrentFilterStatus] = useState<TabStatusType>("all");

    let filterWorks = dataWorks;

    switch (currentFilterStatus) {
        case "manicure": {
            filterWorks = dataWorks.filter(work => work.type === "manicure");
            break
        }
        case "makeUp": {
            filterWorks = dataWorks.filter(work => work.type === "makeUp");
            break
        }
        case "hairdressing": {
            filterWorks = dataWorks.filter(work => work.type === "hairdressing");
            break
        }
        default:
            filterWorks = dataWorks;
            break

    }

    const changeFilterStatus = (value: TabStatusType) => {
        setCurrentFilterStatus(value)
    }

    return (
        <StyledWorks>
            <MyContainer>
                <SectionTitle>Наши работы</SectionTitle>
                <TabMenu listItems={tabMenuItems}
                         changeFilterStatus={changeFilterStatus}
                         currentFilterStatus={currentFilterStatus}
                />

                <FlexContainer wrap={"wrap"} justify={"space-around"}>
                    <AnimatePresence>
                        {filterWorks.map((w) => {
                            return (
                                <motion.div
                                    layout
                                    initial={{opacity: 0}}
                                    animate={{opacity: 1}}
                                    exit={{opacity: 0}}
                                    key={w.id}>
                                    <Photo src={w.src}
                                           alt={w.type}
                                           width={"416px"}
                                           height={"416px"}
                                           key={w.id}
                                    />
                                </motion.div>

                            )
                        })}
                    </AnimatePresence>
                </FlexContainer>


            </MyContainer>
        </StyledWorks>
    );
};


const StyledWorks = styled.section`

  ${FlexContainer} {
    gap: 24px;
  }


  @media ${theme.media.mobile} {
    ${Photo} {
      width: 310px;
      height: 310px;
    }
  }
`