import React from 'react';
import styled from "styled-components";
import {MyContainer} from "../../../components/MyContainer";
import {FlexContainer} from "../../../components/FlexContainer";
import {Icon} from "../../../components/Icon";
import {theme} from "../../../styles/Theme";

const dataCosmetics = [
    {iconId: "cosmetic-4", width: "69", height: "108", viewBox: "0 0 69 108"},
    {iconId: "cosmetic-1", width: "90", height: "120", viewBox: "0 0 90 120"},
    {iconId: "cosmetic-2", width: "190", height: "98", viewBox: "0 0 190 98"},
    {iconId: "cosmetic-3", width: "110", height: "108", viewBox: "0 0 110 108"},
]
export const Cosmetics: React.FC = () => {
    return (
        <StyledCosmetics >
            <MyContainer>
                <FlexContainer justify={"space-around"} wrap={"wrap"}>
                    {dataCosmetics.map((c, index) => {
                        return(
                            <Icon iconId={c.iconId}
                                  width={c.width}
                                  height={c.height}
                                  viewBox={c.viewBox}
                                  key={index}/>
                        )
                    })}
                </FlexContainer>
            </MyContainer>
        </StyledCosmetics>
    );
};

const StyledCosmetics = styled.section`
margin: 150px 0 168px;
  
  @media ${theme.media.mobile}{
    margin: 100px 0 118px;
    
    ${FlexContainer} {
      flex-direction: column;
      align-items: center;
      gap: 35px;
      
    }
  }
`