import React from 'react';
import styled from "styled-components";
import {Link} from "../../../../components/Link";
import {TabMenuItemType, TabStatusType} from "../Works";
import {theme} from "../../../../styles/Theme";

type TabMenuPropsType = {
    listItems: Array<TabMenuItemType>
    currentFilterStatus: TabStatusType
    changeFilterStatus: (value: TabStatusType)=> void
}

export const TabMenu: React.FC<TabMenuPropsType> = (props:TabMenuPropsType) => {
    return (
        <StyledTabMenu>
            <ul>
                {props.listItems.map((i, index) => {

                    return (

                        <li key={index}>
                            <Link active={props.currentFilterStatus === i.status}
                            as={"button"}
                            onClick={() => {props.changeFilterStatus(i.status)}}>
                                {i.title}
                            </Link>
                        </li>
                    )
                })}

            </ul>
        </StyledTabMenu>
    );
};

const StyledTabMenu = styled.nav`
 
ul{
  max-width: 722px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto 60px;
  gap: 10px;
  flex-wrap: wrap;
  
  @media ${theme.media.mobile} {
    justify-content: space-around;
  }
}
`