import React from 'react';
import styled from "styled-components";
import {MyContainer} from "../../../components/MyContainer";
import {FlexContainer} from "../../../components/FlexContainer";
import {Service} from "./service/Service";
import serv1 from "../../../assets/image/imageServices/service-1.webp";
import serv2 from "../../../assets/image/imageServices/service-2.webp";
import serv3 from "../../../assets/image/imageServices/service-3.webp";
import serv4 from "../../../assets/image/imageServices/service-4.webp";
import serv5 from "../../../assets/image/imageServices/service-5.webp";
import serv6 from "../../../assets/image/imageServices/service-6.webp";
import {theme} from "../../../styles/Theme";

const dataServices = [
    {photo: serv1, title: "Визаж"},
    {photo: serv2, title: "Маникюр"},
    {photo: serv3, title: "Косметология"},
    {photo: serv4, title: "Педикюр"},
    {photo: serv5, title: "Парикмахерские услуги"},
    {photo: serv6, title: "Эстетист по телу"},
]
export const Services = () => {
    return (
        <StyledServices>
            <MyContainer>
                <FlexContainer wrap={"wrap"} justify={"space-around"}>
                    {dataServices.map((s, index) => {
                        return (
                            <Service key={index} photo={s.photo} title={s.title}/>
                        )
                    })}
                </FlexContainer>
            </MyContainer>
        </StyledServices>
    );
};

const StyledServices = styled.section`
${FlexContainer} {
  gap: 80px 44px;
}
 
}
`