import React from 'react';
import {Route, Routes, Navigate} from "react-router-dom";
import {Main} from "./main/Main";
import {Workers} from "../layout/section/workers/Workers";
import {Price} from "../layout/section/price/Price";
import {Contacts} from "../layout/section/contacts/Contacts";


export const PATH = {
    MAIN: '/main',
    OUR_TEAM: '/our-team',
    PRICE: '/price',
    CONTACTS: '/contacts'
}
export const Pages = () => {
    return (
        <div>

            <Routes>
                <Route path={"*"} element={<Navigate to={'/main'} />}/>

                <Route path={PATH.MAIN} element={<Main />}/>
                <Route path={PATH.OUR_TEAM} element={<Workers />} />
                <Route path={PATH.PRICE} element={<Price />}/>
                <Route path={PATH.CONTACTS} element={<Contacts />}/>
            </Routes>
        </div>
    );
};

