import React from 'react';
import styled from "styled-components";
import {MyContainer} from "../../../components/MyContainer";
import {SectionTitle} from "../../../components/SectionTitle";
import {FlexContainer} from "../../../components/FlexContainer";
import {Photo} from "../../../components/Photo";
import {ListPrice} from "./listPrice/ListPrice";
import bgPrice from "../../../assets/image/imageBgc/image.webp"
import {theme} from "../../../styles/Theme";

type serviceDataType = {
    src: string
    servicePriceData: Array<servicePriceType>
}

export type servicePriceType = {
    title: string
    text: string
    price: string
}
const serviceData: Array<serviceDataType> = [
    {
        src: bgPrice, servicePriceData: [
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
        ]
    },
    {
        src: bgPrice, servicePriceData: [
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
        ]
    },
    {
        src: bgPrice, servicePriceData: [
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
            {title: "Женская укладка", text: "+ обработк кончинков волос", price: "1 000 ₽"},
        ]
    },
]


export const Price: React.FC = () => {
    const [width, setWidth] = React.useState(window.innerWidth);
    const breakpoint = 1191;

    React.useEffect(() =>{
        const handleWindowResize = () => setWidth(window.innerWidth);
        window.addEventListener("resize", handleWindowResize);
        return () => window.removeEventListener("resize", handleWindowResize)
    },[])
    return (
        <StyledPrice>
            <MyContainer>
                <SectionTitle>Цены на услуги</SectionTitle>
                <Wrap>

                    {serviceData.map((s, index) => {

                        if (index % 2 === 0) {
                            return (<FlexContainer justify={"space-between"}
                                                   wrap={"wrap"}
                                                   key={index}>

                                    <ListPrice servicePriceData={s.servicePriceData}/>
                                    <Photo src={s.src}
                                           width={"636px"}
                                           height={"536px"}/>
                                </FlexContainer>
                            )
                        } if  (width < breakpoint)  {
                            return (<FlexContainer justify={"space-between"}
                                                   wrap={"wrap"}
                                                   key={index}>

                                <ListPrice servicePriceData={s.servicePriceData}/>
                                <Photo src={s.src}
                                       width={"636px"}
                                       height={"536px"}/>
                            </FlexContainer>)
                        } else {
                            return (<FlexContainer justify={"space-between"}
                                                   wrap={"wrap"}
                                                   key={index}>

                                <Photo src={s.src}
                                       width={"636px"}
                                       height={"536px"}/>
                                <ListPrice servicePriceData={s.servicePriceData}/>
                            </FlexContainer>)
                        }

                    })}
                </Wrap>
            </MyContainer>
        </StyledPrice>
    );
};


const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  gap: 80px;
`
const StyledPrice = styled.section`
padding-top: 100px;
  @media ${theme.media.desktop} {
    ${Photo} {
      width: 100%;
      max-width: 636px;
      height: auto;
    }
    ${FlexContainer} {
      justify-content: center;
    }
    
  }
`
